// 
const scrollMsg = msg => {
  window.scrollBy({
    top: msg.offsetHeight + 10, //     
    behavior: 'smooth',
  });
};
//     
const scrollIntoViewOptions = {
  behavior: 'smooth',
  block: 'end',
  inline: 'nearest',
};
// const scrollIntoViewOptionsChat = {
//   behavior: 'smooth',
//   block: 'start',
//   inline: 'nearest',
// };
//      ,  ,    
const scrollChat = chat => {
  const elementPosition = chat.getBoundingClientRect().top; //      
  const consultantSticky = document.querySelector('.consultant_sticky.show');
  const offsetPosition = elementPosition - consultantSticky.offsetHeight; //   
  window.scrollBy({
    top: offsetPosition,
    behavior: 'smooth',
  });
};

